<style scoped>
header {
  background: #07111b;
}
main {
  padding: 36px 0;
  width: 1386px;
  margin: 0 auto;
  display: flex;
}
nav {
  width: 216px;
  background: #ccc;
  text-align: center;
}
nav > img {
  width: 50%;
  border-radius: 50%;
  border: 4px solid white;
}
ul > li {
  padding: 16px;
  list-style: none;
}
nav > h1,
nav > p,
nav > ul {
  padding: 16px 16px;
}
.main {
  margin-left: 70px;
  flex: 1;
}
.router-link-active {
  color: red;
}
</style>
<template>
  <div>
    <header>
      <navs />
    </header>
    <main>
      <nav>
        <h1>
          用户中心
        </h1>
        <img :src="datas.pic" alt="" />
        <p>{{datas.name}}</p>
        <ul>
          <li>
            <router-link to="info">个人信息</router-link>
          </li>
          <li>
            <router-link to="collect">我的收藏</router-link>
          </li>
          <li>
            <router-link to="orderinfo">订单详情</router-link>
          </li>
        </ul>
      </nav>
      <div class="main">
        <router-view></router-view>
      </div>
    </main>
  </div>
</template>
<script>
import navs from "../components/nav";

export default {
  components: {
    navs
  },
  data() {
    return {
      datas: []
    };
  },
  methods: {
    user() {
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/usersinfo",
        params: {
          uid: JSON.parse(window.sessionStorage.getItem("user")).id,
          email: JSON.parse(window.sessionStorage.getItem("user")).email,
          token: JSON.parse(window.sessionStorage.getItem("user")).token,
          name:JSON.parse(window.sessionStorage.getItem("user")).name,
        }
      }).then(res => {
          if (res.data.users) {
          this.datas = res.data.users[0]
          this.$router.push({ name: "user" });
        }
      });
    }
  },
  mounted() {
    this.user();
  }
};
</script>
